<template>
  <div class="component">
    <div class="box">
      <SelectDialog :payload="selectPayload" />

      <!--  -->
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">{{ masterName }}</div>
          <div class="spacer"></div>
          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            class="selectTable"
            :single-select="true"
            @click:row="selectTableItem"
            :search="masterTableSearch"
            :headers="masterHeders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="masterHeders[0].value"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.icon`]="{}">
              <v-icon small class="mr-2"> mdi-arrow-right-bold </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
      <!--  -->
      <div class="col" style="min-width: 50%" v-if="refItem !== null">
        <div class="box rowBox">
          <div class="hyTitle">{{ secoundName }}</div>
          <v-btn
            color="primary"
            dark
            @click="selectItem(secoundName, secoundHeaders, secoundTableList)"
          >
            Select Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="secoundTableSearch"
            append-icon="mdi-magnify"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :search="secoundTableSearch"
            :headers="secoundHeaders"
            :items="secoundTableList"
            :items-per-page="-1"
            :item-key="secoundHeaders[0].value"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, nextTick } from "vue";
import { getRequest } from "@/services/request.js";
import SelectDialog from "@/dialogs/SelectItemDialog.vue";

export default {
  name: "SelectComponent",
  components: {
    SelectDialog,
  },
  props: {
    obj: Object,
  },
  setup(props) {
    const {
      selectKeys,

      masterKeys,
      masterName,
      masterApi,
      masterHeders,
      //
      mappingApi,
      updateApi,
      //
      secoundKeys,
      secoundName,
      secoundApi,
      secoundHeaders,
    } = props.obj;

    masterHeders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });
    secoundHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    const refItem = ref(null);
    const masterTableList = ref([]);
    const secoundTableList = ref([]);
    const masterTableSearch = ref("");
    const secoundTableSearch = ref("");

    let secoundTableListMap = new Map();

    const getSelectedList = (idx) => {
      const [fileName, apiName] = secoundApi;
      getRequest(
        [fileName, apiName + "s"],
        { parent_key: idx },
        (secoundList) => {
          secoundTableList.value = secoundList || [];

          secoundTableListMap = new Map(
            secoundTableList.value.map((item) => {
              return [item[secoundKeys[0]], item];
            })
          );
        }
      );
    };

    const [fileName, apiName] = masterApi;
    getRequest([fileName, apiName + "s"], null, (masterList) => {
      masterTableList.value = masterList || [];
    });

    const selectTableItem = (item, row) => {
      refItem.value = item;

      if (Array.isArray(selectKeys[0]) === true) {
        getSelectedList(item[selectKeys[0][0]]);
      } else {
        getSelectedList(item[selectKeys[0]]);
      }
      row.select(true);
    };

    //
    const selectPayload = ref(null);
    const selectItem = (title, headers) => {
      const [fileName, apiName] = mappingApi;
      getRequest([fileName, apiName], null, (mapList) => {
        selectPayload.value = {
          title,
          headers,
          list: mapList,
          targetList: secoundTableList,
          targetListMap: secoundTableListMap,
          api: updateApi,
          //
          parentItem: refItem.value,
          masterKeys,
          secoundKeys,
          setList: (newList) => {
            secoundTableList.value = newList;
            secoundTableListMap = new Map(
              secoundTableList.value.map((item) => {
                return [item[secoundKeys[0]], item];
              })
            );
          },
        };
      });
    };

    return {
      refItem,

      masterKeys,
      masterName,
      masterApi,
      masterHeders,
      masterTableList,

      secoundKeys,
      secoundName,
      secoundApi,
      secoundHeaders,
      secoundTableList,

      masterTableSearch,
      secoundTableSearch,

      selectTableItem,
      selectItem,
      selectPayload,
    };
  },
};
</script>
