<template>
  <div class="component">
    <div class="box">
      <CreateDialog :payload="createPayload" />
      <DeleteDialog :payload="deletePayload" />
      <UpdateDialog :payload="updatePayload" />

      <!--  -->
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">{{ masterName }}</div>
          <v-btn
            v-if="masterNewItem !== false"
            color="primary"
            dark
            @click="
              createItem(
                'master',
                masterName,
                masterKeys,
                masterApi,
                masterTableList,
                masterHeders
              )
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            class="selectTable"
            :single-select="true"
            @click:row="selectTableItem"
            :search="masterTableSearch"
            :headers="masterHeders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="masterHeders[0].value"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="
                  updateItem(
                    'master',
                    masterName,
                    masterKeys,
                    masterApi,
                    masterTableList,
                    masterHeders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(masterApi, masterTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
            <template v-slot:[`item.icon`]="{}">
              <v-icon small class="mr-2"> mdi-arrow-right-bold </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
      <!--  -->
      <div class="col" style="min-width: 55%" v-if="refItem !== null">
        <div class="box rowBox">
          <div class="hyTitle">{{ secoundName }}</div>
          <v-btn
            color="primary"
            dark
            @click="
              createItem(
                'secound',
                secoundName,
                secoundKeys,
                secoundApi,
                secoundTableList,
                secoundHeaders
              )
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="secoundTableSearch"
            append-icon="mdi-magnify"
            label=""
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :search="secoundTableSearch"
            :headers="secoundHeaders"
            :items="secoundTableList"
            :items-per-page="-1"
            :item-key="secoundHeaders[0].value"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="
                  updateItem(
                    'secound',
                    secoundName,
                    secoundKeys,
                    secoundApi,
                    secoundTableList,
                    secoundHeaders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(secoundApi, secoundTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, nextTick } from "vue";
import { getRequest } from "@/services/request.js";
import DeleteDialog from "@/dialogs/DeleteItemDialog.vue";
import CreateDialog from "@/dialogs/CreateItemDialog.vue";
import UpdateDialog from "@/dialogs/UpdatItemDialog.vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "SelectComponent",
  components: {
    CreateDialog,
    DeleteDialog,
    UpdateDialog,
  },
  props: {
    obj: Object,
  },
  setup(props) {
    const {
      selectKeys,

      masterNewItem,
      masterKeys,
      masterName,
      masterApi,
      masterHeders,
      //
      secoundKeys,
      secoundName,
      secoundApi,
      secoundHeaders,
    } = props.obj;

    masterHeders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });
    secoundHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    const refItem = ref(null);
    const masterTableList = ref([]);
    const secoundTableList = ref([]);

    const masterTableSearch = ref("");
    const secoundTableSearch = ref("");

    const getSelectedList = (code) => {
      const [fileName, apiName] = secoundApi;
      getRequest(
        [fileName, apiName + "s"],
        { parent_key: code },
        (secoundList) => {
          secoundTableList.value = secoundList || [];
        }
      );
    };

    const [fileName, apiName] = masterApi;
    getRequest([fileName, apiName + "s"], null, (masterList) => {
      masterTableList.value = masterList || [];
    });

    const selectTableItem = (item, row) => {
      refItem.value = item;
      if (Array.isArray(selectKeys[0]) === true) {
        getSelectedList(item[selectKeys[0][0]]);
      } else {
        getSelectedList(item[selectKeys[0]]);
      }
      row.select(true);
    };

    //
    const createPayload = ref(null);
    const updatePayload = ref(null);
    const deletePayload = ref(null);

    const createItem = (type, title, key, api, list, fields) => {
      const parentItem = {};
      if (type === "secound") {
        selectKeys.map((key) => {
          if (Array.isArray(key) === true) {
            parentItem[key[1]] = refItem.value[key[0]];
          } else {
            parentItem[key] = refItem.value[key];
          }
        });
      }

      createPayload.value = {
        title,
        api,
        list,
        fields,
        saveKey: key[0],
        //
        parentItem,
      };
    };
    const updateItem = (type, title, key, api, list, fields, item) => {
      const parentItem = {};
      if (type === "secound") {
        selectKeys.map((key) => {
          if (Array.isArray(key) === true) {
            parentItem[key[1]] = refItem.value[key[0]];
          } else {
            parentItem[key] = refItem.value[key];
          }
        });
      }

      updatePayload.value = {
        title,
        api,
        list,
        fields,
        item,
        saveKey: key[0],
        //
        parentItem,
      };
    };
    const deleteItem = (api, list, item) => {
      deletePayload.value = {
        list,
        item,
        api,
      };
    };

    return {
      refItem,
      //
      masterNewItem,
      masterName,
      masterKeys,
      masterApi,
      masterHeders,
      masterTableList,

      secoundKeys,
      secoundName,
      secoundApi,
      secoundHeaders,
      secoundTableList,

      masterTableSearch,
      secoundTableSearch,

      selectTableItem,

      createItem,
      updateItem,
      deleteItem,

      createPayload,
      updatePayload,
      deletePayload,
    };
  },
};
</script>
